    width: 100px;
    height: 6px;
    position: absolute;
    left: 50%;
    top: 0px;
    transform: translateX(-50%);
    background-color: #8c30f5;
    border-radius: 0px 0px 10px 10px;
    display: none;
    animation: s-bar 0.5s;
  }
  .s-box:hover {
